.best-quality-cont {
    padding: 0 2rem;
    .best-quality-desc {
        h2 {
            color: #000;
            margin-bottom: 30px;
        }
    }
}

.best-quality-area {
    padding: 80px 0;
    @include max640px {
        padding: 0;
    }

    .best-quality-img {
        .img-fluid {
            max-width: 500px;
            width: 100%;
        }
    }
}

.best-quality-area--rtl {
    .best-quality-cont {
        padding-right: 6rem;
        @include max768px {
            order: 1;
        }
    }
}
